import classNames from 'classnames/bind';
import { NavLink, Outlet, useLocation, useParams } from 'react-router-dom';

import style from './ResultSearch.module.scss';

const cx = classNames.bind(style);

function PageResultSearch() {
    const { id } = useParams();
    // ly ng dn hin ti sau du  /:
    // const title = id.replace(/\s/g, '%20');
    // ly ra ng dn
    let location = useLocation();
    const MENU_RESULT = [
        {
            title: 'BI HT',
            path: `bai-hat/${id}`,
        },
        {
            title: 'PLAYLIST/ALBUM',
            path: `playlist/${id}`,
        },
        {
            title: 'NGH S/OA',
            path: `artist/${id}`,
        },
        {
            title: 'MV',
            path: `video/${id}`,
        },
    ];

    return (
        <div className={cx('wrapper')}>
            <nav className={cx('header')}>
                <h1 className={cx('title')}>Kt Qu Tm Kim</h1>
                <ul className={cx('menu')}>
                    <li className={cx('item')}>
                        <NavLink
                            to={`tat-ca/${id}`}
                            className={cx(location?.pathname?.indexOf('tat-ca/') >= 0 && 'active')}
                        >
                            TT C
                        </NavLink>
                    </li>
                    {MENU_RESULT?.map((item, index) => (
                        <li className={cx('item')} key={index}>
                            <NavLink to={item?.path} className={(nav) => cx({ active: nav.isActive })}>
                                {item?.title}
                            </NavLink>
                        </li>
                    ))}
                </ul>
            </nav>
            {/* Outlet ch nh ni ni dung xut hin */}
            <Outlet />
        </div>
    );
}

export default PageResultSearch;
